import {
  Component,
  Directive,
  Input,
  Output,
  EventEmitter,
  ChangeDetectionStrategy,
  OnInit,
  OnDestroy,
  Injector,
  Renderer,
  ComponentRef,
  ElementRef,
  TemplateRef,
  ViewContainerRef,
  ComponentFactoryResolver,
  NgZone,
  ViewEncapsulation
} from '@angular/core';
import {VdlPopoverConfig} from './popover-config';
export {VdlPopoverConfig} from './popover-config';

import {listenToTriggers} from './triggers';
import {positionElements} from './positioning';
import {PopupService} from './popup';

@Component({
  moduleId: module.id,
  selector: 'vdl-popover-window',
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  host: {'[class]': '"popover in popover-" + placement', 'role': 'tooltip'},
  styleUrls: ['popover.css'],
  templateUrl: 'popover.html'
})
export class VdlPopoverWindow {
  @Input() placement: 'auto' | 'top' | 'bottom' | 'left' | 'right' = 'auto';
  @Input() title: string;
}

/**
 * A lightweight, extensible directive for fancy popover creation.
 */
@Directive({
  selector: '[vdlPopover]',
  exportAs: 'vdlPopover',
  host: {'(document:click)': '_handleClickOutsidePopover($event)'}
})
export class VdlPopover implements OnInit, OnDestroy {
  /**
   * Content to be displayed as popover.
   */
  @Input() vdlPopover: string | TemplateRef<any>;
  /**
   * Title of a popover.
   */
  @Input() popoverTitle: string;
  /**
   * Width of popover.
   */
  @Input() popoverWidth: number = 320;
  /**
   * Height of popover.
   */
  @Input() popoverHeight: number = 140;
  /**
   * Placement of a popover. Accepts: "auto", "top", "bottom", "left", "right"
   */
  @Input() placement: 'auto' | 'top' | 'bottom' | 'left' | 'right';
  /**
   * Specifies events that should trigger. Supports a space separated list of event names.
   */
  @Input() triggers: string;
  /**
   * A selector specifying the element the popover should be appended to.
   * Currently only supports "body".
   */
  @Input() container: string;
  /**
   * Emits an event when the popover is shown
   */
  @Output() shown = new EventEmitter();
  /**
   * Emits an event when the popover is hidden
   */
  @Output() hidden = new EventEmitter();

  private _popupService: PopupService<VdlPopoverWindow>;
  private _windowRef: ComponentRef<VdlPopoverWindow>;
  private _unregisterListenersFn: any;
  private _zoneSubscription: any;

  /**
   * Set to `true` after the popover opens; used to cancel the document click
   * handler so it doesn't immediately close the popover.
   */
  private _triggered: boolean;

  constructor(private _elementRef: ElementRef,
              private _renderer: Renderer,
              injector: Injector,
              componentFactoryResolver: ComponentFactoryResolver,
              viewContainerRef: ViewContainerRef,
              config: VdlPopoverConfig,
              ngZone: NgZone) {
    this.placement = config.placement;
    this.triggers = config.triggers;
    this.container = config.container;
    this._popupService = new PopupService<VdlPopoverWindow>(
      VdlPopoverWindow, injector, viewContainerRef, _renderer, componentFactoryResolver);

    this._zoneSubscription = ngZone.onStable.subscribe(() => {
      if (this._windowRef) {
        positionElements(
          this._elementRef.nativeElement, this._windowRef.location.nativeElement, this.placement,
          this.container === 'body');
      }
    });
  }

  _handleClickOutsidePopover(e: Event) {
    if (this._triggered) {
      return this._triggered = false;
    }

    if (!this._windowRef) {
      return;
    }

    /* Make sure click didn't happen inside popover */
    if (e.target !== this._windowRef.location.nativeElement &&
      !this._windowRef.location.nativeElement.contains(e.target)) {
      /**
       * Trigger event called 'outsideClick' so that the `listenToTriggers` utility function automatically handles the
       * close action when the close trigger is set to 'outsideClick'.
       */
      let ocEvent = document.createEvent('Event');
      if (ocEvent.initEvent) {
        /* IE-compatible event initialization */
        ocEvent.initEvent('outsideClick', true, true);
      } else {
        /**
         * IE-compatible solution is deprecated. Use Event constructor instead if `initEvent` becomes unavailable.
         */
        ocEvent = new Event('outsideClick');
      }
      this._elementRef.nativeElement.dispatchEvent(ocEvent);
    }
  }

  /**
   * Opens an element’s popover. This is considered a “manual” triggering of the popover.
   */
  open(context?: any) {
    if (!this._windowRef) {
      this._windowRef = this._popupService.open(this.vdlPopover, context);

      if (this.placement === 'auto') {
        let sourceRect = this._elementRef.nativeElement.getBoundingClientRect();

        // If space on bottom to display and no space to display on top
        // If space on bottom to display and no space to display left/right
        if (window.innerHeight >= sourceRect.top + (sourceRect.height / 2) + (this.popoverHeight / 2)
          && (sourceRect.top + (sourceRect.height / 2) - (this.popoverHeight / 2) <= 0
          || (window.innerWidth < sourceRect.right + this.popoverWidth
          && sourceRect.left - this.popoverWidth < 0))) {
          this.placement = 'bottom';
          // If space on top to display and no space to display on bottom
          // If space on top to display and no space to display left/right
        } else if (sourceRect.top + (sourceRect.height / 2) - (this.popoverHeight / 2) > 0
          && (window.innerHeight < sourceRect.top + (sourceRect.height / 2) + (this.popoverHeight / 2)
          || (window.innerWidth < sourceRect.right + this.popoverWidth
          && sourceRect.left - this.popoverWidth < 0))) {
          this.placement = 'top';
          // If space on left and no space right/bottom/top
        } else if (sourceRect.left - this.popoverWidth > 0
          && (window.innerWidth < sourceRect.right + this.popoverWidth
          || window.innerHeight < sourceRect.top + (sourceRect.height / 2) + (this.popoverHeight / 2)
          || sourceRect.top + (sourceRect.height / 2) - (this.popoverHeight / 2) <= 0)) {
          this.placement = 'left';
        } else {
          this.placement = 'right';
        }
      }

      this._windowRef.instance.placement = this.placement;
      this._windowRef.instance.title = this.popoverTitle;

      if (this.container === 'body') {
        window.document.querySelector(this.container).appendChild(this._windowRef.location.nativeElement);
      }

      this._triggered = true;

      // we need to manually invoke change detection since events registered via
      // Renderer::listen() are not picked up by change detection with the OnPush strategy
      this._windowRef.changeDetectorRef.markForCheck();
      this.shown.emit();
    }
  }

  /**
   * Closes an element’s popover. This is considered a “manual” triggering of the popover.
   */
  close(): void {
    if (this._windowRef) {
      this._popupService.close();
      this._windowRef = null;
      this.hidden.emit();
      this._triggered = false;
    }
  }

  /**
   * Toggles an element’s popover. This is considered a “manual” triggering of the popover.
   */
  toggle(): void {
    if (this._windowRef) {
      this.close();
    } else {
      this.open();
    }
  }

  /**
   * Returns whether or not the popover is currently being shown
   */
  isOpen(): boolean {
    return this._windowRef != null;
  }

  ngOnInit() {
    this._unregisterListenersFn = listenToTriggers(
      this._renderer, this._elementRef.nativeElement, this.triggers, this.open.bind(this), this.close.bind(this),
      this.toggle.bind(this));
  }

  ngOnDestroy() {
    this.close();
    this._unregisterListenersFn();
    this._zoneSubscription.unsubscribe();
  }
}
